.video-audio-dialog .material-dialog-head {
  border-bottom: 1px solid #ddd;
  padding: 0 20px 15px;
  margin-bottom: 20px;
  display: flex;
  align-items: start;
}
.video-audio-dialog .material-dialog-head .filter-box {
  flex: 1;
}
.video-audio-dialog .material-dialog-head .upload-wrap {
  text-align: right;
}
.video-audio-dialog .material-dialog-head .upload-wrap p {
  color: #a8a6a6;
  font-size: 14px;
}
.video-audio-dialog .el-dialog__header {
  border-bottom: none !important;
}
.video-audio-dialog .el-dialog__headerbtn .el-dialog__close {
  font-size: 30px;
}
.video-audio-dialog .el-dialog__body {
  height: auto !important;
  padding: 5px 0 !important;
}
.video-audio-dialog .el-tabs__nav-scroll {
  padding: 0 25px;
}
.video-audio-dialog .el-tabs__item {
  font-size: 16px;
}
.video-audio-dialog .dialog-add-material {
  position: absolute;
  top: 80px;
  right: 20px;
  width: 90px;
}
.video-audio-dialog .materials-dialog-table {
  margin: 0 20px;
  width: auto ;
}
.video-audio-dialog .material-dialog-pagination {
  text-align: center;
  margin-bottom: 20px;
}
.video-audio-dialog .el-dialog__footer {
  text-align: center;
}
