.image-module .image-module-container {
  overflow: hidden;
}
.image-module .image-module-container .banner .slide-trans-enter-active {
  transition: all 0.5s;
}
.image-module .image-module-container .banner .slide-trans-enter {
  transform: translateX(100%);
}
.image-module .image-module-container .banner .slide-trans-old-leave-active {
  transition: all 0.5s;
  transform: translateX(-100%);
}
.image-module .image-module-container .banner .slide-show {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.image-module .image-module-container .banner .slide-img {
  width: 100%;
  height: 100%;
  background-color: white;
}
.image-module .image-module-container .banner .slide-img .image-module-image {
  width: 100%;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
}
.image-module .image-module-container .banner .slide-img img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.image-module .image-module-container .banner .slide-pages {
  position: absolute;
  width: 100%;
  bottom: 10px;
  text-align: center;
}
.image-module .image-module-container .banner .slide-pages .normal {
  display: inline-block;
  cursor: pointer;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  margin-right: 6px;
  background-color: white;
}
.image-module .image-module-container .banner .slide-pages .active {
  display: inline-block;
  cursor: pointer;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  margin-right: 6px;
  background-color: gray;
}
.image-module .image-module-container .image-module-image {
  background-position: center;
  background-size: cover;
}
.image-module .no-data {
  width: 100%;
  height: 120px;
  background-color: #4e7be5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
