
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  line-height: 1.6;
  margin: 0;
  font-size: 14px;
}

* {
  margin: 0;
  padding: 0;
}

ul,
ol {
  padding-left: 0;
  list-style-type: none;
}

a img,
fieldset {
  border: 0;
}

a {
  text-decoration: none;
  color: #576B95;
}

a:hover {
  text-decoration: none;
}


button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
  outline: 0;
}

button,
input {
  overflow: visible;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

svg:not(:root) {
  overflow: hidden;
}

/*微信部分ui*/
.wx-btn-default {
  background-color: #fff;
  background-image: linear-gradient(to bottom, #fff 0, #fff 100%);
  border-color: #e7e7eb;
  color: #222;
}

.wx-btn-default:hover {
  background-color: #e7e7eb;
  background-image: linear-gradient(to bottom, #e7e7eb 0, #e7e7eb 100%);
  border-color: #dadbe0;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  color: #222;
}


.wx-btn {
  display: inline-block;
  overflow: visible;
  padding: 0 22px;
  height: 30px;
  line-height: 30px;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  font-size: 14px;
  border-width: 1px;
  border-style: solid;
  cursor: pointer;
  min-width: 60px;
}

.wx-btn-primary {
  background-color: #44b549;
  background-image: linear-gradient(to bottom, #44b549 0, #44b549 100%);
  border-color: #44b549;
  color: #fff;
}

.wx-btn-primary:hover {
  background-color: #2f9833;
  background-image: linear-gradient(to bottom, #2f9833 0, #2f9833 100%);
  border-color: #2f9833;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  color: #fff;
}

.wx-btn-primary button {
  color: #fff;
}

.wx-btn.wx-btn-input {
  min-width: 104px;
  padding: 0;
}

.wx-btn.wx-btn-input button {
  width: 100%;
}

@keyframes tapAnimation {
  0% {
    opacity: 1;
    transform: scale(.96)
  }

  100% {
    opacity: 1;
    transform: scale(1)
  }
}


