.wd-magic-editor > .layout {
  position: relative;
  padding: 0px 15px;
  height: 188px;
  width: 188px;
}
.wd-magic-editor > .layout > .image {
  position: absolute;
  box-sizing: border-box;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.wd-magic-editor .wd-image-editor-image {
  width: 80px;
  height: auto;
}
.wd-magic-editor > .add {
  margin: 10px;
  width: 180px;
  height: 60px;
  background-color: yellow;
  display: flex;
  align-items: center;
}
.wd-magic-editor > .add > .select-image {
  width: 40px;
  height: 40px;
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}
.wd-magic-editor-dialog > .header {
  display: flex;
  justify-content: space-between;
}
.wd-magic-editor-dialog > .header > .uploadBtn {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  background-color: #4e7be5;
  color: white;
}
