div.isAdjustLoading {
  margin-top: 0;
  margin-left: 0;
  margin: 0;
}
.loading-bg {
  position: absolute;
  width: calc(100% - 140px - 20px);
  margin: 48px auto 0;
  background-color: #f2f4f7;
  min-height: calc(100vh - 48px);
  z-index: 99;
  margin-left: 140px;
  box-sizing: border-box;
}
.loading-bg .img-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: calc(100vh - 48px - 20px);
  background-color: #fff;
  margin: 10px;
}
.loading-bg .loading-gif {
  width: 160px;
  height: 100%;
}
.main-title {
  margin: 0;
  font-family: PingFangSC-Semibold;
  font-size: 18px;
  color: #3b4757;
  letter-spacing: 0;
}
.dss-app-with-side-bar .body-div {
  padding: 0;
}
.container {
  border-radius: 2px;
  padding: 20px 0 0;
}
.container [class^='icon-'] {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
}
.container [class^='icon-']:before {
  content: '';
}
