.console-ep-search {
  position: relative;
  background: white;
  border-radius: 2px;
  padding-left: 18px;
  margin: 0 10px 10px;
}
.console-ep-search .el-input__inner {
  box-sizing: border-box;
  width: 100%;
  background: transparent;
  border: 0;
  outline: 0;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  color: #999999;
  padding: 0 10px 0 0;
}
.console-ep-search .el-input__suffix {
  display: none;
}
.console-ep-search .el-icon-search {
  position: absolute;
  top: 6px;
  left: 2px;
  width: 10.2px;
  height: 10.3px;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #fff;
}
::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(0, 0, 0, 0.1);
}
.entry-body {
  background: #ffffff;
  margin: 0 auto;
  min-height: calc(100vh);
}
.entry-body .el-loading-spinner {
  font-size: 20px;
}
.role .el-dialog__title,
.staff-mgr .el-dialog__title {
  color: #303133;
  text-align: center;
  display: inherit;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
}
.role .el-dialog__header,
.staff-mgr .el-dialog__header {
  padding: 20px 0px 10px;
}
.role .el-tree .el-tree-node__content,
.staff-mgr .el-tree .el-tree-node__content {
  border-top: 0;
  margin-top: 5px;
}
.role .wkb-market .el-checkbox,
.staff-mgr .wkb-market .el-checkbox {
  margin-left: 10px;
}
.wkb-market .isHideSubMenu /deep/ div.sub-nav {
  display: none;
}
.wkb-market .isAdjustPageStyle /deep/ div.sub-nav-content-container {
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}
