.top-menu-module {
  font-size: 0px;
}
.top-menu-module .top-menu-module-item-container {
  height: 35px;
  width: 100%;
  overflow-x: auto;
  white-space: nowrap;
  display: flex;
}
.top-menu-module .top-menu-module-item-container .top-menu-module-item {
  display: inline-block;
  margin: 5px 10px;
  color: white;
  font-size: 15px;
  height: 21px;
  line-height: 21px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #ffffff;
}
.top-menu-module .top-menu-module-item-container .indecator {
  background-color: white;
  height: 2px;
  margin-top: 1px;
}
