.top-menu-edit .bottom {
  text-align: center;
  background: #f8f9fe;
  border: 1px solid #bac6d3;
  background: #f0f2f7;
  width: 100%;
  height: 20px;
  line-height: 20px;
  margin-top: 20px;
  font-size: 10px;
  font-family: PingFangSC-Semibold;
  font-weight: 600;
  color: #566980;
}
.com-input1 {
  width: 170px;
  height: 30px;
  font-size: 12px;
}
.com-input1 .el-input__inner {
  height: 30px;
  line-hight: 30px;
}
.top-menu-edit-container .top-menu-edit-item {
  padding: 5px;
  margin: 8px;
  border: 1px solid #f0f2f7;
  position: relative;
  cursor: move;
}
.top-menu-edit-container .top-menu-edit-item .com-close {
  position: absolute;
  right: -8px;
  top: -8px;
}
.top-menu-edit-container .top-menu-edit-item .nav-column {
  display: inline-block;
  margin-top: 10px;
}
.top-menu-edit-container .top-menu-edit-item .nav-column:first-child {
  width: 72px;
  text-align: right;
}
.top-menu-edit-container .top-menu-edit-item .nav-column:last-child {
  width: 170px;
}
.top-menu-edit-container .top-menu-edit-item .link-page {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.top-menu-edit-container .top-menu-edit-item .link-page .delete {
  width: 15px;
  height: 15px;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  margin-left: 5px;
}
.top-menu-edit-container .top-menu-edit-item .cover {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
