.project-module {
  position: relative;
  height: 60px;
  background-color: white;
  padding: 0 10px;
}
.project-module .store-img {
  position: absolute;
  width: 40px;
  height: 40px;
  text-align: center;
  top: 10px;
  overflow: hidden;
  background: #f0f1f5;
  box-sizing: border-box;
}
.project-module .store-img .image {
  width: 22px;
  height: 20px;
  vertical-align: middle;
}
.project-module .project-address {
  margin-top: 2px;
  color: #999;
  font-size: 12px;
}
.project-module .right {
  position: absolute;
  top: 15px;
  right: 10px;
}
.project-module .right .right-icon {
  width: 24px;
  height: 24px;
  display: inline-block;
}
.project-module .right > .right-icon:nth-child(1) {
  background: url(data:image/png;base64,) no-repeat;
  background-size: cover;
}
