.wd-scan-editor .scan-img-preview {
  margin: 20px;
  border: 1px dashed gray;
  position: relative;
}
.wd-scan-editor .scan-img-preview .nav-column {
  display: inline-block;
  margin-top: 10px;
}
.wd-scan-editor .scan-img-preview .nav-column:first-child {
  width: 72px;
  text-align: right;
}
.wd-scan-editor .scan-img-preview .img {
  width: 345px;
  height: 230px;
  display: inline-block;
  vertical-align: top;
  background-size: cover;
  background-position: center;
}
