.text-nav-editor .nav-table {
  position: relative;
  background-color: #fff;
  margin: 15px 10px 0 10px;
  padding: 15px 10px;
  border: 1px dashed #e5e5e5;
}
.text-nav-editor .nav-table:hover .com-close {
  display: inline-block;
}
.text-nav-editor .nav-row {
  margin-bottom: 10px;
}
.text-nav-editor .nav-column {
  display: inline-block;
}
.text-nav-editor .nav-column:first-child {
  width: 72px;
  text-align: center;
}
.text-nav-editor .com-close {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
}
.text-nav-editor .dss-link {
  display: flex;
  align-items: center;
}
.text-nav-editor .link-page {
  display: inline-block;
  align-items: center;
}
.text-nav-editor .link-page .link-name {
  display: inline-block;
}
.text-nav-editor .link-page .delete {
  display: inline-block;
  width: 15px;
  height: 15px;
  cursor: pointer;
  vertical-align: middle;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
