.card-preview-container .card-preview {
  margin-top: 20px;
  width: 135px;
  height: 77px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.card-preview-container .card-preview .card-name {
  position: absolute;
  height: 15px;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  line-height: 15px;
  left: 10px;
  top: 8px;
}
.card-preview-container .card-preview .card-price {
  position: absolute;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  bottom: 8px;
  left: 10px;
  line-height: 12px;
}
.card-preview-container .card-preview .card-type {
  position: absolute;
  bottom: 8px;
  right: 15px;
  height: 13px;
  font-size: 10px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  line-height: 13px;
}
.card-preview-container .card-preview .com-close {
  position: absolute;
  top: 0px;
  right: 0px;
}
.card-preview-container .recharge-card {
  background-image: url(data:image/png;base64,);
}
.card-preview-container .times-card {
  background-image: url(data:image/png;base64,);
}
