.common-dialog {
  display: flex;
  align-items: center;
}
.common-dialog /deep/ .el-dialog {
  margin-top: 0 !important;
  font-size: 14px;
  color: #717378;
}
.common-dialog /deep/ .el-dialog__body {
  padding: 20px 20px !important;
  color: #606266;
  font-size: 14px;
}
.common-dialog /deep/ .el-dialog__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  height: 56px;
  border-bottom: 1px solid #e4e7ed;
}
.common-dialog /deep/ .el-dialog__title {
  font-size: 16px;
  font-family: PingFangSC, PingFangSC-Medium;
  font-weight: 500;
}
.common-dialog /deep/ .el-dialog__headerbtn {
  position: static;
}
.common-dialog /deep/ .el-input {
  width: 248px;
}
.common-dialog /deep/ .el-input__inner {
  height: 36px;
  line-height: 36px;
  border: 1px solid #dce1e6;
  border-radius: 2px;
}
.common-dialog /deep/ .el-input__inner:focus {
  border-color: #dce1e6;
}
.common-dialog /deep/ .el-form-item__label::before {
  display: none;
}
.common-dialog /deep/ .el-form {
  padding: 0 12px;
  text-align: left;
}
.common-dialog /deep/ .el-form-item__label {
  padding-right: 0;
  color: #717378;
}
.common-dialog /deep/ .el-dialog__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  height: 48px;
  border-top: 1px solid #ebeef5;
}
.common-dialog /deep/ .el-dialog__footer /deep/ .el-button {
  padding: 8px 13px;
  margin: 0 13px;
  line-height: 1;
  height: 32px;
  border-radius: 4px !important;
}
.common-dialog /deep/ .el-dialog__footer /deep/ .el-button--default:hover {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
.common-dialog /deep/ .el-dialog__footer /deep/ .el-button--default {
  line-height: 1;
  border: 1px solid #dce1e6;
  color: #717378;
  min-width: 0px;
}
.common-dialog /deep/ .el-dialog__footer /deep/ .el-button--primary {
  line-height: 1;
  border-radius: 2px;
  min-width: 0px;
}
