.select-option.el-select-dropdown__item {
  font-size: 12px;
}
.wd-image-editor {
  width: 367px;
}
.wd-image-editor .timer-limit {
  margin: 10px 0 10px 30px ;
}
.wd-image-editor .timer-limit .add-week-limit-box {
  height: 17px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #858c99;
  line-height: 17px;
  text-align: end;
  margin-top: 2px;
}
.wd-image-editor .timer-limit .add-week-limit-box .add-btn {
  cursor: pointer;
}
.wd-image-editor .timer-limit .until {
  height: 17px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #999999;
  line-height: 17px;
}
.wd-image-editor .timer-limit .duration-box {
  border: 1px solid #e9ecf1;
  box-sizing: border-box;
  padding: 6px;
  margin-top: 10px;
  position: relative;
}
.wd-image-editor .timer-limit .date-pick.el-date-editor.el-input,
.wd-image-editor .timer-limit .date-pick .el-date-editor.el-input__inner {
  width: 117px;
  border-radius: 2px;
}
.wd-image-editor .timer-limit .date-pick.el-input--suffix .el-input__inner {
  padding-right: 0;
}
.wd-image-editor .timer-limit .time-pick.el-date-editor.el-input,
.wd-image-editor .timer-limit .time-pick .el-date-editor.el-input__inner {
  width: 92px;
  border-radius: 2px;
}
.wd-image-editor .timer-limit .time-pick.el-input--suffix .el-input__inner {
  padding-right: 10px;
}
.wd-image-editor .timer-limit .time-del-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  cursor: pointer;
  vertical-align: bottom;
}
.wd-image-editor .style-block {
  position: relative;
  width: 108px;
  height: 88px;
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}
.wd-image-editor .fill-blank {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .off-fill-blank {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .one-row {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .off-one-row {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .fill-screen {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .off-fill-screen {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .pic-rotation {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .off-pic-rotation {
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.wd-image-editor .one-row-upload {
  width: 330px;
  height: 76px;
  background: #e9ecf1;
  margin: 26px auto 0;
  text-align: center;
}
.wd-image-editor .one-row-upload .dss-link {
  padding-top: 24px;
  display: inline-block;
}
.wd-image-editor .one-row-upload div {
  font-size: 12px;
  font-weight: 400;
  color: #858c99;
}
.wd-image-editor .wd-image-editor-image {
  width: 80px;
  height: 40px;
  background-size: 100%;
  background-color: rebeccapurple;
  background-repeat: no-repeat;
  background-position: center;
}
.wd-image-editor > .add {
  width: 180px;
  height: 40px;
  background-color: yellow;
}
.wd-image-editor-dialog > .header {
  display: flex;
  justify-content: space-between;
}
.wd-image-editor-dialog > .header > .uploadBtn {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  background-color: #4e7be5;
  color: white;
}
.fuck-slider {
  display: inline-block;
  position: relative;
  top: -7px;
  left: 0px;
  width: 240px;
}
