.text-nav-module .text-nav-module-container {
  position: relative;
}
.text-nav-module .text-nav-module-container .text-nav-module-title {
  font-size: 16px;
  font-family: PingFangSC-Semibold;
  font-weight: 600;
  color: #373a44;
  display: inline-block;
  font-size: 0;
}
.text-nav-module .text-nav-module-container .text-nav-module-title > div:nth-child(n) {
  display: inline-block;
}
.text-nav-module .text-nav-module-container .text-nav-module-title .left-icon {
  width: 23px;
  height: 20px;
  vertical-align: middle;
}
.text-nav-module .text-nav-module-container .more {
  position: absolute;
  display: inline-block;
  right: 10px;
  line-height: 30px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #373a44;
}
.text-nav-module .text-nav-module-container .more > div {
  display: inline-block;
}
.text-nav-module .text-nav-module-container .more .right-angle {
  background-image: url(data:image/png;base64,);
  background-size: auto 100%;
  background-repeat: no-repeat;
  width: 5px;
  height: 9px;
  margin-bottom: 10px;
}
