.newsModule {
  background-color: white;
  overflow: hidden;
}
.newsModule .item {
  display: flex;
  padding: 0 10px 10px;
}
.newsModule .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  margin-left: 10px;
}
.newsModule .time {
  font-size: 12px;
  color: #999;
}
.newsModule .img-box {
  width: 100px;
  height: 85px;
  background-position: center center;
  background-size: cover;
}
.newsModule .no-data {
  height: 145px;
  background-color: #4e7be5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
