.hot-area-module .hot-area-module-container {
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  height: auto;
}
.hot-area-module .hot-area-module-container .view-drag {
  position: absolute;
  background-color: rgba(59, 212, 252, 0.5);
}
.hot-area-module .hot-area-module-container .layer-tip {
  position: relative;
  width: 100%;
  display: inline-block;
  font-size: 12px;
  color: #000;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
}
.hot-area-module .no-data {
  width: 100%;
  height: 188px;
  background-color: #4e7be5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
