
.popover_inner {
  width: 290px;
  padding: 15px;
  border: 1px solid #d9dadc;
  background: #fff;
  box-shadow: 0 1px 20px 0 #e4e8eb;
  border-radius: 2px;
  position: absolute;
  left: 0;
  z-index: 2;
  line-height: 1;
  top: 45%;
}
.popover_inner .error {
  color: red;
  position: absolute;
  font-size: 12px;
}
.popover_inner .btns {
  margin-top: 20px;
  text-align: center;
}
.popover_inner .el-input__inner {
  height: 28px;
  line-height: 28px;
  margin-top: 10px;
}
.frm_label {
  font-size: 14px;
}
