.com-title {
  height: 40px;
  line-height: 40px;
  margin-top: 14px;
  border-bottom: 1px solid #bac6d3;
}
.com-title span {
  padding-left: 20px;
  line-height: 20px;
  font-size: 14px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: #20252c;
}
.com-header {
  height: 40px;
  line-height: 40px;
  margin-top: 14px;
  border-bottom: 1px solid #bac6d3;
}
.com-header span {
  color: #20252c;
  font-family: PingFangSC-Regular;
  font-weight: 400;
}
.com-header .com-header-title {
  padding: 0 8px;
}
.com-header .el-checkbox__label {
  font-size: 12px;
}
.com-body {
  padding: 0 20px;
}
.com-row {
  font-family: PingFangSC-Regular;
  margin-top: 18px;
}
.com-row .com-column {
  display: inline-block;
}
.com-row .com-column .question {
  width: 16px;
  height: 16px;
  border: 1px solid #666;
  border-radius: 50%;
  text-align: center;
  line-height: 16px;
  font-size: 12px;
  color: #666;
  display: inline-block;
  vertical-align: middle;
  font-style: normal;
  background-color: #eeeeee;
  font-family: PingFangSC-Regular;
  position: relative;
  top: -2px;
}
.com-row .com-column .display-radio {
  margin-top: 5px;
  line-height: 25px;
}
.com-row .com-column:first-child {
  min-width: 74px;
  font-size: 14px;
  font-weight: 400;
  color: #566980;
  vertical-align: top;
  text-align: left;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.com-slide {
  margin-top: -10px;
  width: 200px;
  height: 22px;
  padding: 0 8px;
}
.com-close {
  width: 16px;
  height: 16px;
  display: inline-block;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  cursor: pointer;
}
.com-small-input {
  width: 160px;
  height: 20px;
  font-size: 12px;
}
.com-small-input .el-input__inner {
  height: 20px;
  line-height: 20px;
}
.com-input {
  width: 228px;
  height: 30px;
  font-size: 12px;
}
.com-input .el-input__inner {
  height: 30px;
  line-height: 30px;
}
.com-input-number {
  height: 40px;
}
.com-textarea {
  width: 228px;
  font-size: 12px;
}
.com-color-picker {
  vertical-align: middle;
  cursor: pointer;
}
.com-color-picker .el-color-picker__trigger {
  border: none;
}
.com-upload-button {
  width: 60px;
  height: 60px;
  display: inline-block;
  cursor: pointer;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
/*.com-upload-button:hover {
  border: 2px dashed red;
  color: red;
}*/
.img-preview-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 320px;
  margin: 0 auto;
}
.img-preview-container .img-preview {
  width: 150px;
  height: 150px;
  position: relative;
  margin-top: 20px;
}
.img-preview-container .img-preview .img {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
}
.img-preview-container .img-preview .com-close {
  position: absolute;
  top: 0px;
  right: 0px;
}
.com-row .dss-link {
  display: flex;
  align-items: center;
  color: #2e8dff;
  cursor: pointer;
}
.com-row .link-page {
  display: inline-block;
  align-items: center;
}
.com-row .link-page .link-name {
  display: inline-block;
}
.com-row .link-page .delete {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
}
.com-row .detail-image {
  width: 50px;
  height: 50px;
  display: inline-block;
  vertical-align: top;
  position: relative;
  cursor: pointer;
}
.com-row .detail-image.detail-text {
  width: auto;
  height: auto;
}
.com-row .detail-image .delete {
  width: 15px;
  height: 15px;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  position: absolute;
  top: 3px;
  right: -20px;
}
