/*header统一高度*/
.ellipsis {
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
}
.dss-app-with-side-bar {
  padding-top: 48px;
}
.dss-app-with-side-bar .body-div {
  margin-left: 140px;
  height: calc(100vh - 48px);
  overflow: auto;
  width: calc(100vw - 140px);
  padding: 17px 10px;
  box-sizing: border-box;
  background: #F2F4F7;
}
.limit-line {
  word-break: break-all;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.limit-line.line-3 {
  -webkit-line-clamp: 3;
}
.limit-line.line-4 {
  -webkit-line-clamp: 4;
}
.limit-line.line-5 {
  -webkit-line-clamp: 5;
}
.limit-line.line-6 {
  -webkit-line-clamp: 6;
}
.dss-card {
  background: #ffffff;
  box-shadow: 0 2px 6px 0 rgba(20, 113, 206, 0.14);
  border-radius: 4px;
  padding: 30px;
}
.dss-card .el-header {
  padding: 0;
}
/*表格UI样式*/
.dss-table.el-table {
  width: 100%;
}
.dss-table.el-table thead th,
.dss-table.el-table thead.is-group th {
  background: rgba(234, 240, 244, 0.7);
  border-bottom: none;
  color: rgba(61, 78, 99, 0.8);
  padding: 8px 0 9px;
  text-align: center;
}
.dss-table.el-table td {
  border-bottom: none;
  color: rgba(1, 35, 52, 0.8);
  text-align: center;
}
.dss-table.el-table .el-table__body tr.el-table__row--striped td {
  background: rgba(240, 244, 247, 0.5);
}
.dss-table.el-table td.table-operator button {
  font-size: 14px;
  color: #55a8fd;
  letter-spacing: 0.02px;
  padding: 0;
  margin-left: 22px;
}
.dss-table.el-table td.table-operator .cell > button:first-child,
.dss-table.el-table td.table-operator .cell > span:first-child button {
  margin-left: 0;
}
.dss-table.el-table td.table-operator .btn-del {
  color: #fc6042;
}
.dss-table.el-table::before {
  height: 0;
}
.table-search {
  margin-bottom: 30px;
}
.pull-right {
  float: right;
}
/* 表格删除确认弹框*/
.el-popover {
  box-shadow: 0 2px 9px 0 rgba(52, 153, 254, 0.2);
  border-radius: 4px 4px 4px 0 0 0 4px;
}
.popover-msg {
  text-align: center;
  font-size: 12px;
  color: rgba(1, 35, 52, 0.8);
  letter-spacing: 0;
  margin: 3px 0 4px;
}
.popover-msg i {
  color: #fc6042;
  margin-right: 10px;
}
.popover-operator {
  text-align: center;
  margin: 16px 0 8px;
}
/*分页UI样式*/
.dss-pagination.el-pagination {
  text-align: center;
}
.dss-pagination.el-pagination ul li.active {
  background: #3499fe;
  color: white;
  border-color: #3499fe;
}
.dss-pagination.el-pagination .btn-next,
.dss-pagination.el-pagination .btn-prev,
.dss-pagination.el-pagination .el-pager li {
  margin: 0 4px;
  min-height: 36px;
  height: 36px;
  line-height: 36px;
  min-width: 36px;
  background-color: #ffffff;
  border: 1px solid #d7dde4;
  border-radius: 4px;
  color: #3b4757;
}
.dss-pagination.el-pagination .btn-next {
  padding-left: 8px;
}
.dss-pagination.el-pagination .btn-prev {
  padding-right: 2px;
}
.dss-pagination.el-pagination .el-pagination__total,
.dss-pagination.el-pagination .el-pagination__jump {
  line-height: 36px;
  color: #6f6f6f;
}
.dss-pagination.el-pagination input {
  min-height: 36px;
}
/*对话框新式*/
.dss-dialog .el-dialog {
  background: #ffffff;
  border: 1px solid #dae0e6;
  box-shadow: 0 4px 6px 0 rgba(193, 198, 204, 0.33);
  border-radius: 4px;
}
.dss-dialog .el-dialog__header {
  text-align: center;
  border-bottom: 1px solid #dae0e6;
  padding: 20px;
}
.dss-dialog .el-dialog__title {
  font-size: 16px;
  color: #3499fe;
  line-height: 18px;
}
.dss-dialog .el-dialog__body {
  padding: 20px 20px 30px;
}
.dss-form .required .el-form-item__content::after {
  content: '*';
  position: absolute;
  top: 2px;
  right: 4px;
  font-weight: bold;
  color: #f18585;
  font-size: 22px;
  padding: 3px 10px 0 5px;
  display: block;
  height: 34px;
  background: white;
}
/*按钮UI*/
.dss-btn {
  min-width: 75px;
  font-weight: bold;
}
.dss-btn i {
  font-weight: bold;
}
.dss-primary,
.dss-primary:focus {
  background: #3499fe;
  box-shadow: 0 5px 6px 0 rgba(52, 153, 254, 0.4);
  border-radius: 4px;
  color: white;
  border: none;
  min-width: 75px;
  font-weight: bold;
}
.dss-primary i,
.dss-primary:focus i {
  font-weight: bold;
}
button.dss-primary:active {
  background-color: #3499fe;
}
.dss-primary:hover {
  background: rgba(52, 153, 254, 0.7);
  color: white;
}
.dss-default {
  border: 1px solid #3499fe;
  border-radius: 4px;
  color: #3499fe;
  min-width: 75px;
  font-weight: bold;
}
.dss-default i {
  font-weight: bold;
}
.dss-radio-group .el-radio .el-radio__inner {
  border-color: rgba(1, 35, 52, 0.3);
  width: 16px;
  height: 16px;
}
.dss-radio-group .el-radio .el-radio__inner::after {
  width: 10px;
  height: 10px;
  background-color: #3499fe;
}
.dss-radio-group .el-radio .el-radio__input.is-checked .el-radio__inner {
  border-color: #3499fe;
  background: white;
}
.key-table {
  width: 100%;
  text-align: center;
  color: #39393a;
}
.key-table th,
.key-table td {
  line-height: 42px;
}
.key-table th {
  font-weight: 600;
  border-bottom: solid 1px rgba(151, 151, 151, 0.2);
}
.key-table tbody tr:nth-child(odd) {
  background-color: #f8f9fb;
}
.key-icon {
  position: relative;
  display: inline-block;
  width: 1em;
  height: 1em;
  background-size: 100%;
  background-repeat: no-repeat;
}
.key-icon.key-icon-btn {
  margin-right: 5px;
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.key-icon.disabled {
  opacity: 0.5;
  pointer-events: none;
}
