.image-container .img-preview1 {
  padding: 10px 20px;
  margin: 20px;
  border: 1px dashed gray;
  position: relative;
}
.image-container .img-preview1 .com-close {
  position: absolute;
  right: 0px;
  top: 0px;
}
.image-container .img-preview1 .nav-column {
  display: inline-block;
  margin-top: 10px;
}
.image-container .img-preview1 .nav-column:first-child {
  width: 72px;
  text-align: right;
}
.image-container .img-preview1 .img {
  width: 138px;
  height: 44px;
  display: inline-block;
  vertical-align: top;
  background-size: cover;
  background-position: center;
}
.image-container .img-preview1 .link-page,
.image-container .img-preview1 .link-detail-text {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.image-container .img-preview1 .link-page .delete,
.image-container .img-preview1 .link-detail-text .delete {
  width: 15px;
  height: 15px;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  margin-left: 5px;
  display: inline-block;
}
.image-container .img-preview1 .dss-link1 {
  color: #2e8dff;
  cursor: pointer;
}
.image-container .img-preview1 .detail-image {
  width: 50px;
  height: 50px;
  display: inline-block;
  vertical-align: top;
  position: relative;
  cursor: pointer;
}
.image-container .img-preview1 .detail-image .delete {
  width: 15px;
  height: 15px;
  background: transparent url(data:image/png;base64,) no-repeat 0 0;
  background-size: cover;
  position: absolute;
  top: 3px;
  right: -20px;
  display: inline-block;
}
.image-container .img-preview1 .detail-image.detail-text {
  width: auto;
  height: auto;
}
