.card-module {
  background-color: white;
  overflow: hidden;
  padding-bottom: 10px;
  box-sizing: border-box;
}
.card-module .dev-card-swiper-module {
  display: flex;
}
.card-module .dev-card-swiper-module .card-preview {
  margin-right: 10px;
  width: 135px;
  height: 77px;
  flex-shrink: 0;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.card-module .dev-card-swiper-module .card-preview .card-name {
  display: inline-block;
  height: 15px;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  line-height: 15px;
  margin-left: 10px;
  margin-top: 8px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.card-module .dev-card-swiper-module .card-preview .card-price {
  position: absolute;
  font-size: 12px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
  bottom: 8px;
  left: 10px;
  line-height: 13px;
}
.card-module .dev-card-swiper-module .card-preview .card-type {
  position: absolute;
  bottom: 8px;
  right: 15px;
  height: 13px;
  line-height: 13px;
  font-size: 10px;
  font-family: MicrosoftYaHei;
  color: #ffffff;
}
.card-module .dev-card-swiper-module .recharge-card {
  background-image: url(data:image/png;base64,);
}
.card-module .dev-card-swiper-module .times-card {
  background-image: url(data:image/png;base64,);
}
.card-module .no-data {
  width: 100%;
  height: 77px;
  background-color: #4e7be5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
