.magic-module .magic-module-container {
  overflow: hidden;
  position: relative;
  box-sizing: border-box;
  height: 188px;
}
.magic-module .magic-module-container .image {
  position: absolute;
  box-sizing: border-box;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.magic-module .no-data {
  width: 100%;
  height: 188px;
  background-color: #4e7be5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
}
